import { useContext } from 'react';
// import { FiSearch } from 'react-icons/fi';
import ProjectSingle from './ProjectSingle';
// import FeaturedProject from "./FeaturedProject";
import { ProjectsContext } from '../../context/ProjectsContext';
// import ProjectsFilter from './ProjectsFilter';

const ProjectsGrid = () => {
	const {
		projects,
		// searchProject,
		// setSearchProject,
		// searchProjectsByTitle,
		// selectProject,
		// setSelectProject,
		// selectProjectsByCategory,
	} = useContext(ProjectsContext);

	// 플래그로 분리
	const featuredProjects = projects.filter((p) => p.isFeatured);
	const otherProjects = projects.filter((p) => !p.isFeatured);

  return (
    <section className="py-5 sm:py-10 mt-5 sm:mt-10">
      <div className="mt-16 max-w-4xl mx-auto">
        <h2 className="text-xl sm:text-2xl font-semibold mb-6 text-left text-primary-dark dark:text-primary-light">
          Main Projects
        </h2>
      </div>

      {/* Featured Projects → 카드(Grid) */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-10">
        {featuredProjects.map((project) => (
          <div
            key={project.id}
            className="bg-white dark:bg-primary-dark rounded-lg shadow-md border border-gray-200 dark:border-gray-700 overflow-hidden flex flex-col"
          >
            {/* 이미지 영역 고정 높이 */}
            <div className="w-full h-48 sm:h-56 md:h-64 lg:h-72 overflow-hidden">
              {project.img && (
                <img
                  src={project.img}
                  alt={project.title}
                  className="w-full h-64 object-cover !important"
                />
              )}
            </div>

            {/* 글씨 영역 */}
            <div className="p-4 flex-1 flex flex-col justify-between min-h-[120px]">
              <div>
                <h3 className="text-lg font-semibold text-ternary-dark dark:text-ternary-light">
                  {project.title}
                </h3>
                <p className="mt-1 text-sm text-ternary-dark dark:text-ternary-light">
                  {project.category}
                </p>
              </div>
              {project.description && (
                <p className="mt-2 text-sm text-ternary-dark dark:text-ternary-light line-clamp-3">
                  {project.description}
                </p>
              )}
            </div>
          </div>
        ))}
      </div>



      {/* Other Projects → 리스트(간단 UI) */}
      <div className="mt-16 max-w-4xl mx-auto pt-10">
        <h2 className="text-xl sm:text-2xl font-semibold mb-6 text-left text-primary-dark dark:text-primary-light">
          Other Projects
        </h2>
        <ul className="space-y-4">
          {otherProjects.map((project) => (
            <li
              key={project.id}
              className="flex items-center space-x-4 p-3 border rounded-lg shadow-sm hover:shadow-md transition bg-white dark:bg-gray-800"
            >
              {/* 작은 썸네일 */}
              <img
                src={project.img}
                alt={project.title}
                className="w-12 h-12 object-contain rounded-md"
              />
              {/* 텍스트 */}
              <div>
                <h3 className="text-base font-semibold text-ternary-dark dark:text-ternary-light">
                  {project.title}
                </h3>
                <p className="text-xs text-gray-600 dark:text-gray-400 line-clamp-1">
                  {project.description}
                </p>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </section>
  );
};

export default ProjectsGrid;